.overlay-base {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  background-color: rgba(0, 0, 0, 0);
  opacity: 0;
  transition: 0.5s;
  outline: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay-after {
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 1;
}

.overlay-before {
  background-color: rgba(0, 0, 0, 0);
  opacity: 0;
}

.content-base {
  outline: none;
  position: absolute;
  top: calc(40% - 80px);
  left: 50%;
  transform: translate(-50%, -50%);
  /* width: calc(100vw - 100px);
  height: calc(100vh - 100px); */
  min-width: 480px;
  max-width: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* max-width: 480px;
  max-height: 320px; */
  background-color: #ffffff;
  border-radius: 0px;
  opacity: 0;
  transition-duration: 0.5s;
}

.content-after {
  top: 40%;
  opacity: 1;
}

.content-before {
  top: calc(40% - 80px);
  opacity: 0;
}
